<template>
	<view class="main">
		<view class="foothand" @click="gzbtn" v-show="this.wechatLogo">
			点击关注公众号，接收充电消息通知
		</view>
		<tou_hand ></tou_hand>
		<!-- <tou_hand3 ref="mainindex"></tou_hand3> -->
		<view class="money">
			<view class="moneyone">
				<view class="moneshang" v-show="userlist.realBalance">
					<span style='font-size:30upx;margin-right:6upx;'>¥</span>{{userlist.realBalance}}
				</view>
				<view class="moneshang" v-show="!userlist.realBalance">
					0.00
				</view>
				<view class="monexia">
					账户余额
				</view>
			</view>
			<view class="monez"></view>
			<view class="moneyone">
				<view class="moneshang" v-show="userlist.dummyBalance">
					<span style='font-size:30upx;margin-right:6upx;'>¥</span>{{userlist.dummyBalance}}
				</view>
				<view class="moneshang" v-show="!userlist.dummyBalance">
					0.00
				</view>
				<view class="monexia">
					赠送余额
				</view>
			</view>
		</view>
		<view class="show">
			<view class="one">
				<view class="onetap">
					<navigator url="../nearby_charging/nearby_charging"  hover-class="none">
					<view class="onetapmont">
						<image src="../../static/tabBarimg/k5.png" style='width:52upx;height:52upx;'></image>
						<view class="onetapmonttxt">充电区费率</view>
					</view>
					</navigator>
					<navigator url="../trouble_shooting/trouble_shooting"  hover-class="none">
					<view class="onetapmont">
						<image src="../../static/tabBarimg/k6.png" style='width:50upx;height:51upx;'></image>
						<view class="onetapmonttxt">故障报修</view>
					</view>
					</navigator>
				</view>
				<view class="onetap">
					<navigator url="../remote_charging/remote_charging"  hover-class="none">
					<view class="onetapmont">
						<image src="../../static/tabBarimg/k7.png" style='width:53upx;height:54upx;'></image>
						<view class="onetapmonttxt">远程充电</view>
					</view>
					</navigator>
					<navigator url="../more/more?backid=0"  hover-class="none">
					<view class="onetapmont">
						<image src="../../static/tabBarimg/k8.png" style='width:51upx;height:50upx;'></image>
						<view class="onetapmonttxt">查看更多</view>
					</view>
					</navigator>
				</view>
			</view>
			<view class="tap" v-if="poster==1">
				<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
					<van-swipe-item v-for="(item, index) in posterList" :key="index">
						<image :src=item.img @click="gogd(item.url,item.jump,item.msg,item.libId,item.teamId)"></image>
					</van-swipe-item>
				</van-swipe>
			</view>
		</view>
		<van-popup v-model="gzshow">
			<view class="gztan" >
				<view class="gztanimg">
					<img :src="wechaturl"></img>
					<!-- <img src='../../static/tabBarimg/00000006.jpg'></img> -->
				</view>
				<view class="gztantxt">
					长按识别二维码，关注公众号，接收充电消息通知
				</view>
			</view>
		</van-popup>
		<tabBar tab01="1"></tabBar>
	</view>
</template>

<script>
	import tabBar from '../../components/tabBar/tabBar.vue'
	import AnNoticeBar from '../../components/an-notice-bar/an-notice-bar.vue'
	import tou_hand from '../../components/tou_hand/tou_hand.vue'
	import tou_hand3 from '../../components/tou_hand3/tou_hand3.vue'
	export default {
		components: {
			tabBar,
			AnNoticeBar,
			tou_hand,
			tou_hand3
		},
		mounted() {
			document.querySelector('body').setAttribute('style', 'background-image: linear-gradient(#472E23 2%, #242225 28%,#242225 70%)')
		},
		data() {
			return {
				posterList:[],//轮播图数组
				poster:0,
				gzshow:false,
				wechatLogo:'',//是否显示公众号二维码
				wechaturl:'',//是否显示公众号二维码
				userlist:{},
			}
		},
		onReady() {
		},
		onLoad(option) {
			
		},
		onShow() {
			if(uni.getStorageSync('user').poster==1){
				this.guanggao()
			}
			this.usertap()
			this.wechatLogo=uni.getStorageSync('wechatLogo')
			this.wechaturl=uni.getStorageSync('wechaturl')
			document.querySelector('body').setAttribute('style', 'background-image: linear-gradient(#472E23 2%, #242225 28%,#242225 70%)')
		},
		methods: {
			usertap(){
				this.$base.request('user/userInfo', 'GET')
					.then(res => {
						if(res.data.code==200){
							var userlist = res.data.data
							var userlist1=uni.getStorageSync('user')
							if((userlist.realBalance!=userlist1.realBalance)||(userlist.dummyBalance!=userlist1.dummyBalance)){
								setTimeout(() => {
									this.$refs.mainindex.userdetail()
								}, 100)
							}
							userlist.id=this.pad(userlist.id,8)
							uni.setStorageSync('user',userlist)
							uni.setStorageSync('phone', userlist.phone)
							uni.setStorageSync('Name', userlist.realName)
							this.userlist = uni.getStorageSync('user')
						}
					})
					.catch(err => {
				
					})
			},
			guanggao(){
				let data={
					point:'personal',
					projectId:uni.getStorageSync('user').projectId,
					token:uni.getStorageSync('userToken')
				}
				this.$base.request('poster/getPosterList', 'POST',data)
					.then(res => {
					if(res.data.code==200){
						this.posterList=res.data.data
						if(this.posterList.length>0){
							this.poster=1
						}else{
							this.poster=0
						}
					}else{
						this.poster=0
					}
				})
				.catch(err => {
							
				})
			},
			gogd(url,jump,msg,libId,teamId){
				if(jump=='PU'||jump=='SA'){
					if(url!=0){
						let point='personal'
						this.ggadd(libId,teamId,point)
						window.location.href=url
					}
				}else if(jump=='PA'){
					let point='personal'
					this.ggadd(libId,teamId,point)
					uni.navigateTo({
						url: '../ad_details/ad_details?msg=' +msg 
					});
				}
			},
			ggadd(libId,teamId,point){
				let data={
					systemId:0,
					libId:libId,
					teamId:teamId,
					point:point,
					appid:uni.getStorageSync('user').appid,
					projectId:uni.getStorageSync('user').projectId
				}
				this.$base.request('posterClickMsg/add', 'POST',data)
					.then(res => {
					
				})
				.catch(err => {
							
				})
			},
			pad(num, n) {
			  var len = num.toString().length;
			  while(len < n) {
			    num = "0" + num;
			    len++;
			  }
			  return num;
			},
			
			gzbtn(){
				this.gzshow=true
			}
		
			

		}
	}
</script>

<style lang="scss" scoped>
	.main {
		background-image: linear-gradient(#472E23 2%, #242225 28%,#242225 70%);
		text-size-adjust: 100% !important;
		-webkit-text-size-adjust: 100% !important;  
		-moz-text-size-adjust: 100% !important; 
		width: 100%;
		overflow-x: hidden;
		.foothand{
			width: 90%;
			background: linear-gradient(180deg, #FFA73E 0%, #FF752B 100%);
			border-radius: 24upx;
			padding: 10upx 24upx;
			margin: 0 auto;
			box-sizing: border-box;
			margin-top: 30upx;
			color: #fff;
		}
		.gztan{
			width: 650upx;
			background-color: #FFFFFF;
			text-align: center;
			padding: 70upx 0;
			.gztanimg{
				width: 350upx;
				height: 350upx;
				margin: 0 auto;
				img{
					width: 350upx;
					height: 350upx;
				}
			}
			
			.gztantxt{
				margin-top: 30upx;
				font-size: 20upx;
			}
		}
		.money{
			width: 650upx;
			height: 176upx;
			background: #343136;
			border-radius: 24upx;
			margin: 30upx auto;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 90upx;
			box-sizing: border-box;
			.moneyone{
				text-align: center;
				.moneshang{
					font-size: 36upx;
					font-family: PingFangSC-Semibold, PingFang SC;
					font-weight: 600;
					color: #FFFFFF;
					margin-bottom: 8upx;
				}
				.monexia{
					font-size: 26upx;
					font-family: PingFangSC-Regular, PingFang SC;
					font-weight: 400;
					color: #FFFFFF;
				}
			}
			.monez{
				width: 2upx;
				height: 70upx;
				background: #fff;
			}
		}
		.show {
			.one{
				width: 100%;
				box-sizing: border-box;
				padding: 0 50upx;
				.onetap{
					width: 100%;
					display: flex;
					justify-content: space-between;
					margin-bottom: 30upx;
					.onetapmont{
						width: 300upx;
						height: 120upx;
						background: #FFDFCE;
						border-radius: 24upx;
						display: flex;
						align-items: center;
						justify-content: center;
						box-sizing: border-box;
						
						.onetapmonttxt{
							font-size: 28upx;
							font-family: PingFangSC-Medium, PingFang SC;
							font-weight: 500;
							color: #242225;
						}
						image{
							margin-right: 24upx;
						}
					}
				}
			}
			.tap {
				width: 649upx;
				height: 300upx;
				border-radius: 16upx;
				margin: 30upx auto;
				margin-bottom: 0upx;
				padding-bottom: 150upx;
				image {
					width: 649upx;
					height: 300upx;
					border-radius: 16upx;
				}
			}
			.tap1 {
				width: 649upx;
				height: 300upx;
				border-radius: 16upx;
				margin: 30upx auto;
				margin-bottom: 0upx;
				padding-bottom: 150upx;
				image {
					width: 649upx;
					height: 300upx;
					border-radius: 16upx;
				}
			}
		}
	
		
	}
</style>
